import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
// import { PROMOTIONS } from '../../app/shared/PROMOTIONS';
import { baseUrl } from '../../app/shared/baseUrl';
import { mapImageURL } from '../../utils/mapImageURL';


const initialState = {
    promotionsArray: [],
    isLoading: true,
    errMsg: ''
};

export const fetchPromotions = createAsyncThunk(
    'promotions/fetchPromotions',
    async () => {
        const response = await fetch(baseUrl + 'promotions');
        if (!response.ok) {
            return Promise.reject('Unable to fetch, status: ' + response.status);
        }
        const data = await response.json();
        return data;
    }
);

const promotionsSlice = createSlice({
    name: 'promotions',
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(fetchPromotions.pending, (state) => {
                state.isLoading = true;
                state.errMsg = ''; // Reset error when a new request starts
            })
            .addCase(fetchPromotions.fulfilled, (state, action) => {
                state.isLoading = false;
                state.promotionsArray = mapImageURL(action.payload);
                state.errMsg = '';
            })
            .addCase(fetchPromotions.rejected, (state, action) => {
                state.isLoading = false;
                state.errMsg = action.payload || 'Fetch failed';
            });
    }
});

export const promotionsReducer = promotionsSlice.reducer;

export const selectFeaturedPromotion = (state) => {
    return {
        featuredItem: state.promotions.promotionsArray.find(
            (promotion) => promotion.featured
        ),
        isLoading: state.promotions.isLoading,
        errMsg: state.promotions.errMsg
    };
};